header {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 8px;
  background: linear-gradient(to left top, blue, red);
  color: #ffffff;
}
header h1 {
  font-size: 30px;
}
header h2 {
  font-size: 25px;
}
header p {
  font-weight: 700;
  font-size: 20px;
}
@media (min-width: 300px) {
  header {
    font-weight: 200;
  }
}
@media (min-width: 600px) {
  header {
    font-weight: 200;
  }
}
@media (max-width: 500px) {
  header {
    font-size: 30px;
  }
}
@media (max-width: 700px) {
  header {
    font-size: 40px;
  }
}

main {
  width: 1000px;
  margin: 0 auto;
}

pre,
code {
  width: 100%;
  white-space: pre-line;
}

footer {
  font-size: 20px;
  text-align: center;
}
@media (max-width: 700px) {
  footer {
    font-size: 30px;
  }
}
@media (max-width: 500px) {
  footer {
    font-size: 40px;
  }
}

section {
  height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.js-fadein {
  display: inline-block;
  margin: 0 20px;
  height: 60px;
  width: 60px;
  background: #888;
  visibility: hidden;
}